<template>
	<div class="skills">
		<ul class="skills-list">
			<li v-for="skill in skills" :style="{background: skill.background}" class="skills-list-card" :key="skill.id">
				<div class="skill-card-logo">
					<img :src="skill.logoSrc" :title="skill.logoSrc">
				</div>
				<h3 class="skill-card-title">{{skill.title}}</h3>
				<p>{{skill.content}}</p>
			</li>
		</ul>
	</div>
</template>

<script>
	import CentralStorage from '../../CentralStorage.js'
	export default {
		data() {
			return {
				skills: []
			}
		},
		created() {
			this.skills = CentralStorage.skills
		}
	}

</script>


<style>
	.skills {
		margin-bottom: 85px;
	}

	.skills-list {
		display: flex;
		justify-content: space-between;
		padding-left: 10px;
	}

	.skills-list-card {
		position: relative;
		flex-shrink: 0;
		text-align: center;
		font-size: 13px;
		line-height: 1.3;
		color: #fff;
		font-family: 'Open Sans';
		padding: 40px 15px 20px;
		width: calc(100% / 5 - 20px);
	}

	.skill-card-logo {
		position: absolute;
		width: 120px;
		top: 0;
		left: 50%;
		transform: translate(-48%, -84%);
	}

	.skill-card-title {
		text-transform: uppercase;
		color: #CCAE3F;
		font-size: 18px;
		letter-spacing: -1px;
		margin-bottom: 15px;
	}

	@media only screen and (max-width: 1200px) {
		.skills-list {
			flex-wrap: wrap;
			padding: 0 20px;
			justify-content: flex-start;
			margin-right: -30px;
			margin-bottom: -110px;
		}

		.skills-list-card {
			width: calc(100% / 3 - 30px);
			height: 200px;
			margin-right: 30px;
		}

		.skills-list-card:not(:last-child) {
			margin-bottom: 120px;

		}
	}

	@media only screen and (max-width: 480px) {
		.skills {
			margin-bottom: 40px;
		}

		.skills-list {
			margin-right: 0;
			margin-bottom: 0;
		}

		.skills-list-card {
			width: 100%;
			height: 200px;
			margin-right: 0;
		}
	}

</style>



