<template>
	<div id="app" class="wrapper">
		<app-header></app-header>
		<app-main></app-main>
		<app-footer></app-footer>
	</div>
</template>

<script>
	import Header from './components/header/Header'
	import Main from './components/main/Main'
	import Footer from './components/footer/Footer'

	export default {
		name: 'App',
		components: {
			'app-header': Header,
			'app-main': Main,
			'app-footer': Footer
		}
	}

</script>
<style>
	html,
	body,
	div,
	span,
	applet,
	main,
	object,
	iframe,
	h1,
	h2,
	h3,
	h4,
	h5,
	h6,
	p,
	blockquote,
	pre,
	a,
	abbr,
	acronym,
	address,
	big,
	cite,
	code,
	del,
	dfn,
	em,
	img,
	ins,
	kbd,
	q,
	s,
	samp,
	small,
	strike,
	strong,
	sub,
	sup,
	tt,
	var,
	b,
	u,
	i,
	center,
	dl,
	dt,
	dd,
	ol,
	ul,
	li,
	fieldset,
	form,
	label,
	legend,
	table,
	caption,
	tbody,
	tfoot,
	thead,
	tr,
	th,
	td,
	article,
	aside,
	canvas,
	details,
	embed,
	figure,
	figcaption,
	footer,
	header,
	hgroup,
	menu,
	nav,
	output,
	ruby,
	section,
	summary,
	time,
	mark,
	audio,
	video {
		margin: 0;
		padding: 0;
		border: 0;
		font-size: 100%;
	}

	/* HTML5 display-role reset for older browsers*/
	article,
	aside,
	details,
	main,
	figcaption,
	figure,
	footer,
	header,
	hgroup,
	menu,
	nav,
	section {
		display: block;
	}

	blockquote,
	q {
		quotes: none;
	}

	blockquote:before,
	blockquote:after {
		content: '';
		content: none;
	}

	q:before,
	q:after {
		content: '';
		content: none;
	}

	table {
		border-collapse: collapse;
		border-width: 0;
		padding: 0;
		margin: 0;
	}

	html {
		height: 100%;
		-webkit-text-size-adjust: none;
		-ms-text-size-adjust: none;
	}

	input,
	textarea {
		color: #333;
		outline: none;
		border-radius: 0;
		-moz-border-radius: 0;
		-webkit-border-radius: 0;
		-webkit-appearance: none;
	}

	input[type=button],
	input[type=submit] {
		cursor: pointer;
		-webkit-appearance: none;
		outline: none;
	}

	input[type=search] {
		-webkit-appearance: none;
	}

	button {
		cursor: pointer;
		-webkit-appearance: none;
		outline: none;
	}

	td,
	form {
		margin: 0;
		padding: 0;
	}

	a,
	i {
		color: #000100;
		transition: all .3s linear;
		text-decoration: none;
		outline: none;
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
		display: inline-block;
	}

	* {
		box-sizing: border-box;
	}

	*:before,
	*:after {
		box-sizing: border-box;
	}

	ul {
		list-style: none;
	}

	li {
		cursor: pointer;
	}

	span {
		display: inline-block;
	}

	img {
		display: block;
		max-width: 100%;
		width: 100%;
	}

	body {
		font-family: 'Roboto';
		overflow-x: hidden;
	}

	.wrapper {
		max-width: 100%;
		width: 100%;
		overflow: hidden;
	}

	.container {
		max-width: 1180px;
		width: 100%;
		margin: 0 auto;
	}

</style>



