<template>
	<header>
		<div class="container">
			<topline>
				<template #phone>
					<div class="top-line-link">
						<img :src="icon.phone" alt="phone">
						<a class="phone-link" href="tel: +7(495)5332211">+7(495)533-22-11</a>
					</div>
				</template>

				<template #mail>
					<div class="top-line-link">
						<img :src="icon.mail" alt="mail">
						<a class="mail-link" href="mailto: stroymaterial@mail.ru">straymaterial@mail.ru</a>
					</div>
				</template>
				<template #search>
					<form action="#!" class="form-search">
						<input class="form-search-input" type="search" name="search" placeholder='Поиск по сайту...'>
						<button class="form-search-btn"><img :src="icon.search" alt="zoom"></button>
					</form>
				</template>
			</topline>
		</div>
		<header-nav-menu></header-nav-menu>
		<carousel></carousel>
	</header>
</template>

<script>
	import Topline from './Topline';
	import NavMenu from './navmenu/NavMenu';
	import Carousel from './slider/Carousel';
	import CentralStorage from './../../CentralStorage.js';


	export default {
		name: 'Header',
		data() {
			return {
				icon: {}
			}
		},
		created() {
			this.icon = CentralStorage.icon
		},
		components: {
			topline: Topline,
			'header-nav-menu': NavMenu,
			'carousel': Carousel,
			CentralStorage

		}
	}

</script>

<style>
	@media (max-width: 1200px) {
		.top-line {
			display: none;
		}
	}

</style>



