<template>
	<div class="top-line">
		<slot name="phone"></slot>
		<slot name="mail"></slot>
		<slot name="search"></slot>
	</div>
</template>


<script>
	export default {
		name: 'Topline',
		data() {
			return {}
		},
		created() {}
	}

</script>

<style scoped>
	.container {
		max-width: 1180px;
		width: 100%;
		margin: 0 auto;
	}

	/*
	HEADER
*/

	.top-line {
		color: #B5B5B5;
		display: flex;
		align-items: center;
		padding: 6px 0 6px 14px;
	}

	.top-line-link {
		display: flex;
		align-items: center;
	}

	.top-line-link:first-child {
		margin-right: 98px;
	}

	.top-line-link img {
		margin-right: 10px;
		width: auto;
	}

	.top-line-link a {
		font-size: 14px;
		color: #8b8888;
	}

	.form-search {
		display: flex;
		margin-left: auto;
	}

	.form-search-input {
		border: none;
		color: #333;
		width: 200px;
	}

	.form-search-btn {
		padding: 0;
		border: none;
		background: none;
	}

	@media only screen and (max-width: 1200px) {
		.top-line {
			padding-left: 40px;
			padding-right: 40px;
		}
	}

	@media only screen and (max-width: 769px) {
		.top-line {
			display: none;
		}
	}

</style>



