<template>
	<div class="carousel">
		<transition :name="transition" v-for="(slide, i) in slides" :key="slide.index">
			<div :style="slide.url" class="carousel-slide" v-show="index === i"></div>
		</transition>
	</div>
</template>

<script>
	export default {
		props: ['slides'],
		data() {
			return {

			}
		},
		computed: {
			index() {
				return this.$parent.index;
			},
			transition() {
				if (this.$parent.direction) return 'slide-' + this.$parent.direction;
			}
		}
	}

</script>

<style>
	.carousel {
		position: absolute;
		overflow: hidden;
		display: flex;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		z-index: 0;
	}

	.carousel-slide {
		height: 100%;
		width: 100%;
		position: absolute;
		top: 0;
		left: 0;
	}

	.slide-right-enter-active {
		animation: slideRightIn .5s;
	}

	.slide-right-leave-active {
		animation: slideRightOut .5s;

	}

	@keyframes slideRightIn {
		from {
			transform: translateX(100%);
		}

		to {
			transform: translateX(0%);
		}
	}

	@keyframes slideRightOut {
		from {
			transform: translateX(0%);
		}

		to {
			transform: translateX(-100%);
		}
	}

	.slide-left-enter-active {
		animation: slideLeftIn .5s;
	}

	.slide-left-leave-active {
		animation: slideLeftOut .5s;

	}

	@keyframes slideLeftIn {
		from {
			transform: translateX(-100%);
		}

		to {
			transform: translateX(0%);
		}
	}

	@keyframes slideLeftOut {
		from {
			transform: translateX(0%);
		}

		to {
			transform: translateX(100%);
		}
	}

	@media only screen and (max-width: 1200px) {
		.carousel-slide {
			background-position: right center !important;
		}
	}

</style>



