<template>
	<div class="carousel-nav">
		<button class="carousel-btn carousel-btn--next" @click.prevent="next"></button>
		<button class="carousel-btn carousel-btn--prev" @click.prevent="prev"></button>
		<ul class="carousel-pagination">
			<li v-for="(slide,i) in slides" @click="goto(i)" :class="{active: index == i}"></li>
		</ul>
	</div>
</template>

<script>
	export default {
		props: ['slides'],
		data() {
			return {}
		},
		computed: {
			index() {
				return this.$parent.index;
			}
		},
		methods: {
			next() {
				(this.index >= this.$parent.slidesCount) ? this.$parent.index = 0: this.$parent.index++;
				this.$parent.direction = 'right'
			},
			prev() {
				this.index ? this.$parent.index-- : this.$parent.index = this.$parent.slidesCount;
				this.$parent.direction = 'left'
			},
			goto(index) {
				this.$parent.direction = index > this.index ? 'right' : 'left';
				this.$parent.index = index;

			}
		}
	}

</script>

<style>
	.carousel-btn {
		position: absolute;
		z-index: 999;
		padding: 10px;
		background: none;
		border: solid #b4b4b0;
		border-width: 8px 8px 0 0;
		top: 50%;
		transition: .3s;
	}

	.carousel-btn:hover {
		border-color: #FEB902;
	}

	.carousel-btn--prev {
		left: 0;
		transform: rotate(-130deg) translateY(140%);
	}

	.carousel-btn--next {
		right: 3%;
		transform: rotate(45deg) translateY(-130%);
	}

	.carousel-pagination {
		position: absolute;
		left: 50%;
		bottom: 6%;
		display: flex;
		transform: translate(-40%, -50%)
	}

	.carousel-pagination li {
		width: 17px;
		height: 17px;
		border-radius: 50%;
		background: #A4A4A3;
		border: 3px solid #fff;
		transition: .2s;
	}

	.carousel-pagination li.active {
		background: #fff;
		border-color: #F8B710;
	}

	.carousel-pagination li:not(:last-child) {
		margin-right: 14px;
	}

	.carousel-pagination li:hover {
		background: #fff;
		border-color: #F8B710;
	}

	@media only screen and (max-width: 1200px) {
		.carousel-btn--next {
			right: 6%;
		}
	}

	@media only screen and (max-width: 769px) {
		.carousel-btn--next {
			right: 10%;
		}
	}

	@media only screen and (max-width: 480px) {
		.carousel-pagination {
			bottom: 2%;
		}

		.carousel-btn {
			display: none;
		}
	}

</style>



