<template>
	<div class="carousel-viewport">
		<carousel-slide :slides="slides" />
		<div class="carousel-body container">
			<h1 class="carousel-title">
				Комплексное снабжение<br>
				<span class="mark">стройматериалами</span>
			</h1>
			<p class='carousel-content'>
				{{content}}
			</p>


			<div class="btn-row">
				<a href="#!" class="btn btn-more" @click="getMore = !getMore">подробнее</a>
				<a href="#!" class="btn btn-calc">рассчитать</a>
			</div>
			<carousel-control :slides="slides"></carousel-control>
		</div>
	</div>

</template>

<script>
	import CentralStorage from '../../../CentralStorage.js';
	import CarouselSlide from './CarouselSlide';
	import CarouselControl from './CarouselControl';
	export default {
		data() {
			return {
				index: 0,
				slides: [],
				direction: null,
				getMore: false,
			}
		},
		created() {
			this.slides = CentralStorage.slider;
		},
		mounted() {
			this.slides.forEach((slide, i) => {
				slide.index = i
			})
		},
		computed: {
			content() {
				let size = 145;
				let text = this.slides[this.index].content;
				let length = text.length;

				if (!this.getMore) {
					// Обрезка текста
					if (length > size) text = text.slice(0, size) + ' ...';
					return text
				} else return text
			},
			slidesCount() {
				return this.slides.length - 1
			},
			transition() {
				if (this.$parent.direction) return 'textSlide-' + this.$parent.direction;
			}
		},
		components: {
			CarouselSlide,
			CarouselControl
		}
	}

</script>

<style>
	.carousel-viewport {
		position: relative;
		overflow: hidden;
		min-height: 449px;
		display: flex;
	}

	.carousel-body {
		position: relative;
		display: flex;
		flex-direction: column;
		padding: 70px 82px 20px;
	}

	.carousel-title {
		font-family: sans-serif;
		position: relative;
		font-weight: bold;
		text-transform: uppercase;
		font-size: 40px;
		line-height: 1.2;
		letter-spacing: -.2px;
		color: #fff;
		margin-bottom: 28px;
		letter-spacing: .3px
	}

	.carousel-title .mark {
		color: #FFB404;
	}

	.carousel-content {
		color: #fff;
		line-height: 1.2;
		letter-spacing: .5px;
		margin-bottom: 48px;
		max-width: 470px;
		overflow: hidden;
		max-height: 120px;
		text-overflow: ellipsis;
	}

	.btn {
		text-transform: uppercase;
		font-weight: 600;
		font-size: 13px;
		width: 160px;
		height: 45px;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		border-radius: 4px;
		border: 2px solid #fff;
		color: #fff;
	}

	.btn-more {
		margin-right: 17px;
	}

	.btn:hover {
		background: #fff;
		color: #E3BB50;
	}

	@media only screen and (max-width: 769px) {
		.carousel-body {
			padding: 40px 68px 20px;
		}

		.carousel-title {
			font-size: 34px;
		}
	}

	@media only screen and (max-width: 655px) {
		.carousel-title {
			font-size: 28px;
		}
	}

	@media only screen and (max-width: 480px) {
		.carousel-body {
			padding: 0 40px;
			justify-content: center;
		}

		.btn-row {
			display: flex;
			flex-direction: column;
		}

		.btn-more {
			margin-right: 0;
			margin-bottom: 10px;
		}

		.carousel-title {
			font-size: 22px;
		}

		.carousel-content {
			max-height: 100%;
			margin-bottom: 20px;
		}
	}

</style>



