<template>
	<div class="section-tools">
		<h2 class="section-title"><strong>Стройматериалы</strong> для любых работ</h2>
		<ul class="cards-tools-list">
			<li v-for="card of toolsList" class="card-tools" :key="card.id">
				<img :src="card.imgSrc">
				<div class="card-tools-body">
					<h3 class="card-tools-title">
						{{card.title}}
					</h3>
					<p class="card-tools-content">
						{{card.content}}
					</p>
				</div>

			</li>
		</ul>
	</div>
</template>

<script>
	import CentralStorage from '../../CentralStorage.js'
	export default {
		data() {
			return {
				toolsList: [],
			}
		},
		created() {
			this.toolsList = CentralStorage.toolsList
		}
	}

</script>

<style>
	.section-tools {
		padding-left: 30px;
	}

	.section-title {
		position: relative;
		font-size: 36px;
		letter-spacing: -1px;
		font-weight: 300;
		margin-bottom: 44px;
	}

	.section-title:before {
		content: '';
		position: absolute;
		width: 40px;
		height: 3px;
		bottom: -10px;
		left: 0;
		background: #F5BB34;
	}

	.cards-tools-list {
		display: flex;
		justify-content: space-between;
	}

	.card-tools {
		width: calc(100% / 3 - 18px)
	}

	.card-tools-body {
		background: #F1F1F1;
		padding: 20px 20px 23px;
	}

	.card-tools-title {
		font-size: 22px;
		color: #525252;
		line-height: 1.3;
		margin-bottom: 28px;
	}

	.card-tools-content {
		font-size: 15px;
		line-height: 1.4;
		color: #828481;
	}

	@media only screen and (max-width: 1200px) {
		.section-tools {
			padding: 20px;
		}
	}

	@media only screen and (max-width: 769px) {
		.cards-tools-list {
			flex-wrap: wrap;
		}

		.card-tools {
			width: calc(100% / 2 - 8px);
			margin-bottom: 20px;
		}

		.card-tools-title {
			font-size: 18px;
		}
	}

	@media only screen and (max-width: 480px) {
		.section-title:before {
			bottom: 40px;
		}

		.card-tools {
			width: 100%;
		}
	}

</style>



