<template>
	<div class="nav-line">
		<div class="container">
			<div class="navigation">
				<div class="logo">
					<a href="/"></a>
				</div>
				<topline>
					<template #phone>
						<div class="top-line-link">
							<img :src="icon.phone" alt="phone">
							<a class="phone-link" href="tel: +7(495)5332211">+7(495)533-22-11</a>
						</div>
					</template>
					<template #mail>
						<div class="top-line-link">
							<img :src="icon.mail" alt="mail">
							<a class="mail-link" href="mailto: stroymaterial@mail.ru">straymaterial@mail.ru</a>
						</div>
					</template>
				</topline>
				<nav>
					<div class="burger" :class="{'is-active': active}" @click="toggleActive">
						<div class="burger-box">
							<div class="burger-inner"></div>
						</div>
					</div>
					<ul class="nav-menu" :data-animate="animate">
						<topline>
							<template #search>
								<form action="#!" class="form-search">
									<input class="form-search-input" type="search" name="search" placeholder='Поиск по сайту...'>
									<button class="form-search-btn">
										<img :src="icon.search" alt="search">
									</button>
								</form>
							</template>
						</topline>
						<li v-for="item of navMenu" :class="['nav-menu-li',{'has-submenu': item.hasSubmenu}]">
							<a :href="item.url">
								{{item.name}}
							</a>
						</li>
					</ul>
				</nav>
			</div>
		</div>
	</div>
</template>

<script>
	import CentralStorage from '../../../CentralStorage.js';
	import Topline from '../Topline';
	export default {
		name: 'NavMenu',
		components: {
			'topline': Topline
		},
		data() {
			return {
				navMenu: [],
				active: false,
				windowWidth: 0,
				windowHeight: 0,
				direction: null,
				icon: {},
			}
		},
		created() {
			this.navMenu = CentralStorage.navMenu;
			this.icon = CentralStorage.icon;
		},
		computed: {
			animate() {
				if (this.direction) return 'slide-' + this.direction;
			}
		},
		methods: {
			toggleActive() {
				this.active = !this.active;
				this.active ? this.direction = 'left' : this.direction = 'right'

			},
		},
		mounted() {}
	}

</script>

<style scoped>
	.top-line {
		display: none;
	}

	.nav-line {
		background: #3376BC;
	}

	.navigation {
		display: flex;
		align-items: center;
		padding: 0 8px;
	}

	.logo {
		flex-grow: 1;
		flex-shrink: 0;
		height: 42px;
		overflow: hidden;
	}

	.logo a {
		display: block;
		height: 100%;
		background: url(/static/img/logo.png) no-repeat;
	}

	nav {
		align-self: stretch;
		margin-left: auto;
	}

	.burger {
		display: none;
		position: relative;
		z-index: 10;
		padding: 10px;
		cursor: pointer;
		transition-duration: .15s;
		-webkit-transform: matrix(1, 0, 0, 1, 0, 0);
		transform: matrix(1, 0, 0, 1, 0, 0);
	}

	.burger-box {
		width: 30px;
		height: 21px;
		display: -ms-flexbox;
		display: flex;
		position: relative;
		-webkit-transform: matrix(1, 0, 0, 1, 0, 0);
		transform: matrix(1, 0, 0, 1, 0, 0);
	}

	.burger-inner {
		top: 50%;
		margin-top: -1.5px;
		transition-duration: 0.44s;
		transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
	}

	.burger-inner:before,
	.burger-inner:after {
		content: '';
		display: block;
	}

	.burger-inner:before {
		top: -9px;
		transition: top 0.2s 0.5s ease-in, opacity 0.2s ease-in;
	}

	.burger-inner:after {
		bottom: -9px;
		transition: bottom 0.2s 0.5s ease-in, transform 0.44s cubic-bezier(0.55, 0.055, 0.675, 0.19);

	}

	.burger-inner,
	.burger-inner:before,
	.burger-inner:after {
		width: 100%;
		height: 3px;
		background: #fff;
		border-radius: 0;
		position: absolute;
	}

	.is-active .burger-inner {
		transform: rotate(225deg);
		transition-delay: .24s;
		transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
	}

	.is-active .burger-inner:before {
		top: 0;
		opacity: 0;
		transition: top 0.2s ease-out, opacity 0.2s 0.24s ease-out;
	}

	.is-active .burger-inner:after {
		bottom: 0;
		transform: rotate(-90deg);
		transition: bottom 0.2s ease-out, transform 0.44s 0.24s cubic-bezier(0.215, 0.61, 0.355, 1);
	}

	.nav-menu {
		display: flex;
		margin-left: auto;
	}

	.nav-menu-li {
		text-transform: uppercase;
		position: relative;
	}

	.nav-menu-li:not(:last-child) {
		margin-right: 37px;
	}

	.nav-menu-li a {
		position: relative;
		font-weight: bold;
		padding: 34px 0;
		display: block;
		color: #fff;
	}

	.nav-menu-li a:after {
		content: '';
		position: absolute;
		opacity: 0;
		display: block;
		width: 100%;
		height: 5px;
		bottom: 0;
		left: 0;
		background: rgba(255, 216, 0, 0.93);
		transition: .3s;
	}

	.nav-menu-li a:hover {
		color: rgba(255, 216, 0, 0.93);
	}

	.nav-menu-li a:hover:after {
		opacity: 1;
	}

	.nav-menu-li.has-submenu a:after {
		content: none;
	}

	.nav-menu-li.has-submenu a:before {
		content: '+';
		display: block;
		position: absolute;
		right: -15px;
		top: 50%;
		transform: translateY(-50%);
		color: rgba(255, 216, 0, 0.93);
	}

	[data-animate='slide-left'] {
		animation: slideLeft .5s forwards;
	}

	[data-animate='slide-right'] {
		animation: slideRight .5s cubic-bezier(0.55, 0.055, 0.675, 0.19);
	}

	@keyframes slideLeft {
		0% {
			opacity: 0;
			transform: translateX(100%);
		}

		100% {
			opacity: 1;
			box-shadow: -2px -2px 14px rgba(0, 0, 0, .2);
			transform: translateX(0);
		}
	}

	@keyframes slideRight {
		0% {
			opacity: 1;
			transform: translateX(0);
		}

		100% {
			opacity: .3;
			transform: translateX(100%);
		}
	}

	@media only screen and (max-width: 1200px) {
		.navigation {
			padding: 0 40px;
		}

		.nav-menu-li a {
			padding: 24px 0;
			font-size: 14px;
		}
	}

	@media only screen and (max-width: 1070px) {


		.navigation .top-line-link:first-child {
			margin-right: 10px;
		}

		.nav-menu .top-line {
			padding: 0 20px;
			margin-bottom: 20px;
		}

		.form-search {}

		.form-search-input {
			padding: 10px;
			width: 85%;
			flex-grow: 0;

		}

		.form-search-btn {
			width: 25%;
			/*			display: none;*/
		}

		.nav-line {
			position: relative;
		}

		.navigation {
			padding-top: 15px;
			padding-bottom: 15px;
			width: 100%;
		}

		nav {
			align-self: center;
		}

		.burger {
			display: block;
		}

		.nav-menu {
			display: flex;
			flex-direction: column;
			position: absolute;
			top: 100%;
			right: 0;
			width: 200px;
			z-index: 9999;
			padding: 20px 0;
			background: #3376BC;
			border-radius: 0 0 6px 6px;
			transform: translateX(100%);
			transition: .3s ease;
		}

		.nav-menu .nav-menu-li {
			margin: 0;
		}

		.nav-menu .nav-menu-li a {
			padding: 20px 30px;
		}

		.nav-menu .nav-menu-li a:hover {
			background: rgba(8, 42, 155, 0.9);
		}

		.nav-menu-li.has-submenu a:before {
			left: 10px;
			right: 0;
			transform: translateY(-60%);
		}
	}

	@media only screen and (max-width: 769px) {
		.top-line {
			display: block;
			padding: 0 20px;
			margin-left: auto;
		}
	}

	@media only screen and (max-width: 655px) {

		.logo {
			overflow: hidden;
			width: 50px;
			flex-grow: 0;
		}
	}

	@media only screen and (max-width: 480px) {
		.navigation {
			padding: 10px;
		}

	}

</style>



