<template>
	<main>
		<div class="container">
			<Skills />
			<Tools />
		</div>
	</main>
</template>


<script>
	import Skills from './Skills'
	import Tools from './Tools'

	export default {
		data() {
			return {}
		},
		components: {
			Skills,
			Tools
		}
	}

</script>
<style>
	main {
		padding: 160px 0 64px;
	}

	@media only screen and (max-width:480px) {
		main {
			padding-bottom: 0;
		}
	}

</style>



